import * as React from "react";
import "./GalleryCard.scss";

import Mask from "../../assets/misc/Paper.png";

export const GalleryCard = ({ src, onClick }) => {
    return (
        <div className="gallery-card-wrapper" onClick={onClick}>
            <div className="gallery-card-mask" style={{ webkitMaskImage: `url(${Mask})` }}>
                <img src={src} className="gallery-card-image" />
            </div>
        </div>
    );
}