import YouTube from "react-youtube"
import "./YoutubeVideo.scss"
import * as React from "react";

export const YoutubeVideo = ({ id, containerClassName }) => {

    const opts = {
        height: '100%',
        width: '100%',
        playerVars: {
            cc_load_policy: 0,
            fs: 0,
            iv_load_policy: 3,
            modestbranding: 1,
            rel: 0,
            showinfo: 0,
        },
    };

    return (
        <YouTube videoId={id} opts={opts} containerClassName={containerClassName} />
    )
}