import * as React from "react";
import "./FeatureIcon.scss";

export const FeatureIcon = ({ svg, text }) => {

    const SvgIcon = svg;

    return (
        <div className="feature-icon-wrapper flex-column">
            <SvgIcon viewBox="0 0 512 512" className="feature-icon-icon" />
            <div className="feature-icon-text">{text}</div>
        </div>
    )
}