import React, { useState } from "react";
import "./GallerySection.scss";
import { GalleryCard } from "../../components/GalleryCard/GalleryCard"
import ImgsViewer from "react-images-viewer";

import i0 from "../../assets/gallery/0.jpg";
import i1 from "../../assets/gallery/1.jpg";
import i2 from "../../assets/gallery/2.jpg";
import i3 from "../../assets/gallery/3.jpg";
import i4 from "../../assets/gallery/4.jpg";
import i5 from "../../assets/gallery/5.jpg";

import HeroImageRef from "../../assets/backdrop/hero-image.jpg";

export const GallerySection = () => {

    const galleryArray = [
        i0,
        i1,
        i2,
        i3,
        i4,
        i5
    ]

    const [currImg, setCurrImg] = useState(0);
    const [viewerIsOpen, setViewerIsOpen] = useState(false);

    const openViewer = (imgNo) => {
        setViewerIsOpen(true);
        setCurrImg(imgNo);
    }

    const closeViewer = () => setViewerIsOpen(false);

    const onClickPrev = () => {
        if (currImg > 0) {
            setCurrImg(currImg - 1);
        }
        else {
            setCurrImg(galleryArray.length - 1);
        }
    }

    const onClickNext = () => {
        if (currImg <= galleryArray.length - 1) {
            setCurrImg(currImg + 1);
        }
        else {
            setCurrImg(0);
        }
    }

    return (
        <>
            <div className="gallery-section">
                <div className="gallery-section-content-wrapper flex-column">
                    <div className="gallery-section-content-title">
                        GALLERY
                    </div>
                    <div className="gallery-section-content-grid-layout flex-row">
                        {
                            galleryArray.map((img, idx) => <GridItem src={img} onClick={() => openViewer(idx)} />)
                        }
                    </div>
                </div>
                <div className="gallery-section-disclaimer">
                    * Please note that the development is in progress and the design is subject to changes.
                </div>
            </div>

            <ImgsViewer
                imgs={galleryArray.map((img) => { return { src: img } })}
                currImg={currImg}
                isOpen={viewerIsOpen}
                onClose={closeViewer}
                onClickPrev={onClickPrev}
                onClickNext={onClickNext}
                backdropCloseable
            />
        </>
    );
}

const GridItem = ({ src, onClick }) => (
    <div className="gallery-grid-item">
        <GalleryCard src={src} onClick={onClick} />
    </div>
)